import React from 'react';
import Page from './components/matrix/index'
import Features from './components/features/features'
import {
  createBrowserRouter, createHashRouter,
} from "react-router-dom";

const App = createHashRouter([
  {
    path: "/",
    element: <Page />
  },
  {
    path: "/features",
    element: <Features />
  }
])

export default App;
