import React from 'react'
import './input.css'
import TextField from '@mui/material/TextField';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { DesktopDatePicker } from '@mui/x-date-pickers/DesktopDatePicker';
import Tetrad from './../../generator/main'
import Matrix from './matrix'

export default function Input() {
    const [date, changeDate] = React.useState(new Date('June 10, 1998'))
    const [count, setCount] = React.useState(0)
    const [sum, setSum] = React.useState(0)
    const array = React.useRef([])

    React.useEffect(() => {
        array.current = Tetrad(date.getDate(), date.getMonth()+1, date.getFullYear())
        console.log(array.current)
        setCount(array.current.length)
        setSum(date.getDate() + date.getMonth()+1 + Math.floor(date.getFullYear()/100) + Math.floor(date.getFullYear()%100))


        // axios.post('http://localhost:3000/api/v1/generateSquares', {
        //     'date': date
        // }).then((res) => {
        //     console.log(res.data.data)

        //     var arr = res.data.data
        //     array.current = arr
        //     setCount(arr.length)
        //     setSum(date.getDate() + date.getMonth()+1 + Math.floor(date.getFullYear()/100) + Math.floor(date.getFullYear()%100))
        // })
    }, [date])

    return(
        <div className="input">
            <div className="picker">
                <h4>
                    Pick your date of birth
                </h4>
                <LocalizationProvider dateAdapter={AdapterDateFns}>
                    <DesktopDatePicker
                        inputFormat="dd/MM/yyyy"
                        value={date}
                        onChange={changeDate}
                        renderInput={(params) => <TextField {...params} />}
                    />
                </LocalizationProvider>
            </div>
            
            <h2>
                You have {count} Magic Squares
            </h2>
            <h3>
                Sum: {sum}
            </h3>
            <div className="wrapper">
                    {
                        array.current.map((arr, i) => {
                            return <Matrix key={i} data={arr}/>
                        })
                    }
            </div>
        </div>
    )
}