function shuffle(array) {
    for (let i = array.length - 1; i > 0; i--) {
        let j = Math.floor(Math.random() * (i + 1));
        let temp = array[i];
        array[i] = array[j];
        array[j] = temp;
    }

    return array
}

export default function Tetrad(date, month, year) {
    let A = date
    let B = month
    let C = Math.floor(year / 100)
    let D = Math.floor(year % 100)
    let threshold1 = 99
    let results = []
    for (let H = threshold1; H > 0; H--) {
        let E = B + C - H
        let F = C + D - E
        let G = A + B - H
        let temp = {
            A: A,
            B: B,
            C: C,
            D: D,
            E: E,
            F: F,
            G: G,
            H: H
        }
        let tempArr = Object.values(temp)
        if (new Set(tempArr).size !== tempArr.length || tempArr.some(num => num > 99) || tempArr.some(num => num < 1)) {
            continue
        } else {
            for (let M = B + C; M > 0; M--) {
                let P = B + C - M
                let J = H + P - B
                let K = E + M - C
                let I = B + D - K
                let L = A + C - J
                let N = K + L - M
                let O = I + J - P
                temp["I"] = I
                temp["J"] = J
                temp["K"] = K
                temp["L"] = L
                temp["M"] = M
                temp["N"] = N
                temp["O"] = O
                temp["P"] = P

                tempArr = Object.values(temp)
                if (new Set(tempArr).size !== tempArr.length || tempArr.some(num => num > 99) || tempArr.some(num => num < 1)) {
                    continue
                } else {
                    results.push(tempArr)
                }
            }
        }
    }
    return shuffle(shuffle(results))
}

